<template>
  <div id="app">
    <MyHeader @addTodo="addTodo"></MyHeader>
    <MyList :todoList="todoList" />
    <MyFooter
      v-show="todoList.length"
      :todoList="todoList"
      @selectAll="selectAll"
      @deleteFinished="deleteFinished"
    ></MyFooter>
  </div>
</template>

<script>
import MyHeader from "./components/MyHeader";
import MyList from "./components/MyList";
import MyFooter from "./components/MyFooter";

export default {
  name: "App",
  components: {
    MyHeader,
    MyList,
    MyFooter,
  },
  data() {
    return {
      todoList: JSON.parse(localStorage.getItem("data")) || [],
    };
  },
  mounted() {
    this.$bus.$on("changeState", this.changeState);
    this.$bus.$on("deleteTodo", this.deleteTodo);
    this.$bus.$on("updateTodo", this.updateTodo);
  },
  beforeDestroy() {
    this.$bus.$off(["changeState", "deleteTodo", "updateTodo"]);
  },
  methods: {
    addTodo(todo) {
      this.todoList.unshift(todo);
    },
    changeState(todoId) {
      this.todoList.forEach((item) => {
        if (item.id === todoId) {
          item.finished = !item.finished;
        }
      });
    },
    selectAll(flag) {
      this.todoList.forEach((item) => (item.finished = flag ? true : false));
    },
    deleteTodo(id) {
      this.todoList = this.todoList.filter((item) => item.id !== id);
    },
    deleteFinished() {
      this.todoList = this.todoList.filter((item) => !item.finished);
    },
    updateTodo(todoId, value) {
      this.todoList.forEach((item) => {
        if (item.id === todoId) {
          item.todo = value;
        }
      });
    },
  },
  watch: {
    todoList: {
      deep: true,
      handler(val) {
        localStorage.setItem("data", JSON.stringify(this.todoList));
      },
    },
  },
};
</script>

<style>
body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #b1d0e0;
}
#app {
  margin-top: 100px;
}

