<template>
  <div class="item">
    <div>
      <input
        :id="todoObj.id"
        type="checkbox"
        :checked="todoObj.finished"
        @change="changeHandle"
      />
      <label
        v-show="!todoObj.isEdit"
        :for="todoObj.id"
        v-text="todoObj.todo"
      ></label>
      <input
        type="text"
        ref="inputTodo"
        v-show="todoObj.isEdit"
        :value="todoObj.todo"
        @blur="blurHandle($event)"
        @keyup.enter="blurHandle($event)"
      />
    </div>
    <div>
      <a v-show="!todoObj.isEdit" @click="editHandle">编辑</a>
      <a @click="deleteHandle">删除</a>
    </div>
  </div>
</template>

<script>
export default {
  name: "MyItem",
  props: ["todoObj"],
  methods: {
    changeHandle() {
      this.$bus.$emit("changeState", this.todoObj.id);
    },
    deleteHandle() {
      this.$bus.$emit("deleteTodo", this.todoObj.id);
    },
    editHandle() {
      this.todoObj.isEdit = true;
      this.$nextTick( () => {
        this.$refs.inputTodo.focus();
      });
    },
    blurHandle(e) {
      this.todoObj.isEdit = false;
      if (!e.target.value.trim()) return alert("输入不能为空！");
      this.$bus.$emit("updateTodo", this.todoObj.id, e.target.value);
    },
  },
};
</script>

<style scoped>
.item {
  width: 100%;
  height: 40px;
  line-height: 40px;
  display: flex;
  justify-content: space-between;
}
.item:hover {
  background-color: #6998ab;
  border-radius: 5px;
}
.item:hover a {
  opacity: 1;
}

.item label {
  font-size: 20px;
  margin-left: 10px;
}

.item a {
  margin-right: 5px;
  color: #fff;
  padding: 3px 8px;
  background-color: #406882;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  opacity: 0;
}
.item a:hover {
  background-color: #1a374d;
}
</style>