<template>
  <div class="footer">
    <input
      id="select-all"
      type="checkbox"
      :checked="curr === total"
      @change="selectAllHandle($event)"
    />
    <label for="select-all">全选</label>
    <span>{{ curr }}/{{ total }}</span>
    <a @click="$emit('deleteFinished')">删除选中</a>
  </div>
</template>

<script>
export default {
  name: "MyFooter",
  props: ["todoList"],
  computed: {
    curr() {
      return this.todoList.filter((item) => item.finished).length;
    },
    total() {
      return this.todoList.length;
    },
  },
  methods: {
    selectAllHandle(e) {
      this.$emit("selectAll", e.target.checked);
    },
  },
};
</script>

<style scoped>
.footer label {
  font-size: 20px;
  margin-left: 10px;
}
.footer span {
  margin-left: 10px;
}

.footer a {
  float: right;
  padding: 5px 10px;
  color: #fff;
  font-size: 14px;
  cursor: pointer;
  border-radius: 5px;
  background-color: #6998ab;
  transition: background-color 0.3s;
}
.footer a:hover {
  background-color: #1a374d;
}
</style>