<template>
  <div>
    <input class="search" type="text" v-model="todo" @keyup.enter="addHandle" />
  </div>
</template>

<script>
import { nanoid } from "nanoid";
export default {
  name: "MyHeader",
  data() {
    return {
      todo: "",
    };
  },
  methods: {
    addHandle() {
      const temp = {
        id: nanoid(),
        todo: this.todo,
        finished: false,
        isEdit: false
      };
      this.$emit('addTodo', temp)
      this.todo = "";
    },
  }
};
</script>

<style scoped>
.search {
  width: 400px;
  font-size: 18px;
  outline: none;
  border: 1px solid #6998ab;
  border-radius: 5px;
  padding: 5px;
}
</style>